.main-nav {
    position: fixed;
    width: 100%;
    top: 0;

    /* display: flex;
    justify-content: space-between;
    align-items: center; */

    z-index: 1111;
    background-color: #F8F9FA;
    color: var(--text-color);
}

.max-width-nav {
    max-width: 1100px;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
}

.main-brand {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    text-align: center;
    margin-left: 1rem;
}

.nav-header {
    color: var(--primary-color);
    font-size: 1.5rem;
    font-weight: 500;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
}

/* .main-outer-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 60px;
    height: 60px;
    background-color: #fff;
    border-radius: 50%;
    transition: 0.3s;
} */

.main-brand img {
    padding: 0.2rem 0.5rem 0.5rem 0.5rem;
    width: 60px;
    height: 58px;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.main-list-inline {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}


.main-title {
    margin: 1rem;
    padding: 0.5rem;

    /* font-size: 1rem; */
    /* font-weight: 600; */
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.main-title a {
    color: var(--text-color);
    text-decoration: none;
    cursor: pointer;
}

.main-title a:hover {
    color: var(--promary-color);
}

.main-title a:focus {
    color: var(--on-hover);
}

#main-hidden {
    display: -ms-flexbox;
    display: flex;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

.main-nav-menu {
    display: none;
    font-size: 2rem;
    cursor: pointer;
    -webkit-transition: 0.3s;
    -o-transition: 0.3s;
    transition: 0.3s;
}

@media only screen and (max-width: 900px) {
    /* .main-title {
        font-size: 0.9rem;
        transition: 0.3s;
    } */
}

@media only screen and (max-width: 860px) {
    .nav-header {
        font-size: 1.2rem;
    }
}

@media only screen and (max-width: 800px) {
    .nav-header {
        font-size: 0.9rem;
    }
}
@media only screen and (max-width: 750px) {
    .nav-header {
        display: none;
    }
}

@media only screen and (max-width: 800px) {
    /* .main-title {
        font-size: small;
        transition: 0.3s;
    } */
}

@media only screen and (max-width: 720px) {
    .main-list-inline {
        display: none;
        -webkit-transition: 0.3s;
        -o-transition: 0.3s;
        transition: 0.3s;
    }

    .main-nav-menu {
        display: -ms-flexbox;
        display: flex;
    }

    #main-hidden {
        position: absolute;
        top: 4.3rem;
        left: 0;
        width: 100%;
        height: auto;
        background-color: #F8F9FA;

        -ms-flex-direction: column;

            flex-direction: column;
        -ms-flex-pack: start;
            justify-content: flex-start;
        /* align-items: center; */

        -webkit-box-shadow: 0 .125rem .25rem rgba(0,0,0,.075)!important;

                box-shadow: 0 .125rem .25rem rgba(0,0,0,.075)!important;

        /* border-bottom-right-radius: 1rem;
        border-bottom-left-radius: 1rem; */
        /* transition: 0.3s; */
        -webkit-animation: fadein 0.3s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
                animation: fadein 0.3s;
    }

    @keyframes fadein {
        from { opacity: 0; }
        to   { opacity: 1; }
    }
    
    /* Firefox < 16 */
    
    /* Safari, Chrome and Opera > 12.1 */
    @-webkit-keyframes fadein {
        from { opacity: 0; }
        to   { opacity: 1; }
    }
    
    /* Internet Explorer */
    
    /* Opera < 12.1 */

    .main-title {
        font-size: 1.05rem;
        margin-top: 0.2rem;
        margin-bottom: 0.2rem;
        margin-left: 0.6rem;
    }

    .main-brand {
        margin-left: 0;
    }

    .main-nav {
        padding: 0 1rem;
    }

    .main-list-inline {
        padding-left: 0.5rem;
    }
    /* .main-brand img, .main-outer-img {
        width: 50px;
        height: 50px;
    } */
}


